<template>
  <div class="fill">

    <template v-if="loading">
      <img
        :src="src"
        style="width:120px"
      />
      <span class="status">{{$t('device_loading')}}.....</span>
    </template>

    <template v-if="!loading">
      <span>{{$t('device_title_a')}}</span>
      <span v-html="$t('device_title_b')"></span>
      <img :src="src" />
      <span class="status">{{ status }}</span>

      <div class="btn-groups">
        <base-button
          class="ko"
          @click="resultClick(true)"
        >{{$t('button_next')}}</base-button>
      </div>
    </template>
  </div>
</template>

<script>
import browser_loading from './../assets/images/loading.gif';
import browser_setting_cn from './../assets/images/browser_setting_cn.gif';
import browser_setting_en from './../assets/images/browser_setting_en.gif';
export default {
  name: "Device",
  data() {
    return {
      src: browser_loading,
      srclist: {
        zh_CN: browser_setting_cn,
        en_US: browser_setting_en
      },
      cameraItem: {},
      microphoneItem: {},
      cameras: [],
      microphones: [],
      status: "",
      loading: true,
    }
  },
  methods: {
    init() {
      const that = this;
      this.videoStream.init().then(list => {
        console.log("CAMERA :::", list[0]);
        console.log("MIC    :::", list[1]);
        console.log("AUDIO  :::", list[2]);
        that.cameras = list[0];
        that.microphones = list[1];
        if (that.cameras.length == 0 && that.microphones.length) {
          that.resultClick();
        } else {
          that.openDevice();
        }
      }).catch(err => {
        console.log(err);
      });
    },
    openDevice() {
      const that = this;
      if (this.videoStream) {
        this.videoStream.close();
        if (this.cameras.length) {
          this.cameraItem = this.cameras[0];
          this.videoStream.cameraId = this.cameraItem.deviceId;
        }

        if (this.videoStream.microphones.length) {
          this.microphoneItem = this.microphones[0];
          this.videoStream.microphoneId = this.microphoneItem.deviceId;
        }

        setTimeout(out => {
          that.loading = false;
          that.src = that.srclist[window.vm.$i18n.locale];
        }, 600);

        this.videoStream.streamID = Math.floor(Math.random() * 10000);
        this.videoStream.createStream().then(function (stream) {
          console.log(' 1', that.videoStream.access);
          that.status = that.$t('device_allow_text');
          
          that.resultClick();
        }).catch(function (err) {
          console.log(' 2', that.videoStream.access);
          that.status = that.$t('device_faild_text');
          that.src = that.srclist[that.$i18n.locale];
          that.resultClick();
        });
      }
    },
    resultClick() {
      this.videoStream.close();
      this.videoStream.cameras.filter(item => item.code = 0);
      this.videoStream.microphones.filter(item => item.code = 0);
      this.$emit('complete', this.videoStream.access);
    }
  },
  mounted() {
    this.init();
  }
}
</script>

