<template>
  <div id="app">
    <!-- <device  @complete="deviceComplete"></device> -->
    <span class="page-label"></span>
    <PagePicture
      class="page-picture"
      :type="step"
    ></PagePicture>

    <transition
      appear
      mode="out-in"
      leave-active-class="animated zoomOut"
    >
      <ReadyTest
        v-if="step == 1"
        @complete="readyComplete"
      ></ReadyTest>

      <Home
        v-else
        @ready="reportReady"
        @reset="testReset"
        @complete="testComplete"
      ></Home>
    </transition>

  </div>
</template>

<script>
import Home from './components/Home.vue';
import PagePicture from './components/PagePicture.vue';
import ReadyTest from './components/ReadyTest.vue';
import Device from './components/Device.vue';
export default {
  name: 'App',
  components: {
    Home,
    Device,
    PagePicture,
    ReadyTest,
  },
  data() {
    return {
      step: 1,
    }
  },
  methods: {
    queryString(name) {
      const reg = new RegExp('(^|&)' + name + '=([^&]*)(&|$)', 'i');
      const r = window.location.search.substr(1).match(reg);
      return r ? decodeURI(r[2]) : null;
    },
    readyComplete() {
      this.step = 2;
    },
    reportReady() {
      this.step = 3;
    },
    testReset() {
      this.step = 1;
    },
    testComplete(data) {

    },
    languageChange() {
      const language = this.queryString('lang');
      if (language) {
        switch (language) {
          case 'en_US':
            this.$i18n.locale = 'en_US';
            break;
          case 'zh_CN':
            this.$i18n.locale = 'zh_CN';
            break;
          default:
            this.$i18n.locale = 'zh_CN';
        }
      }else{
        const browserLanguage = navigator.language;
        switch(browserLanguage){
          case 'en-US':
            this.$i18n.locale = 'en_US';
            break;
          case 'zh-CN':
            this.$i18n.locale = 'zh_CN';
            break;
          default:
            this.$i18n.locale = 'zh_CN';
        }
      }
    }
  },
  mounted() {
    this.languageChange();
  }
}
</script>

