import socketio from "socket.io-client";
import event from 'events';

class Socket extends event.EventEmitter {
    constructor() {
        super();
        this.count = 5;
        this.connected = false;
    }

    init({
        path,
        url
    }) {
        this.manager = {
            path,
            secure: true,
            transports: ['websocket'], // ['websocket', 'polling']
            reconnection: true, //
            reconnectionAttempts: this.count, //
            reconnectionDelay: 2000, //
            reconnectionDelayMax: 10000, //,2
            timeout: 20000 //
        };
        this.url = url;
        Object.assign(this, this.manager);
        this.connected = false;
        this.socket = socketio.connect(url, this.manager);
        this.listen(this.socket)
    }

    listen(socket) {

        socket.on('connected', () => {
            console.log("socket io", this.url, this.manager.path, "connected success");
            socket.emit("authenticate", "token");
        })

        // authenticated
        socket.on('authenticated', () => {
            this.connected = true;
            this.emit('connected');
        });

        //,err
        socket.on("connect_error", () => {
            console.error("socket io", "connect error");
            this.connected = false;
        });

        //
        socket.on("connect_timeout", () => {
            console.log("socket io", "connect_timeout");
            this.connected = false;
        });

        //
        socket.on('disconnect', () => {
            console.log("socket io", 'disconnect');
            this.connected = false;
            this.emit('disconnect', this.connected);
        });

        //,num
        socket.on('reconnect', (num) => {
            console.log("socket io", 'reconnect', num);
        });

        //
        socket.on('reconnect_attempt', () => {
            console.log("socket io", 'reconnect attempt');
        });

        //, num
        socket.on('reconnecting', (num) => {
            console.log("socket io", 'reconnecting', num);
            this.emit('reconnect', num, this.manager.reconnectionAttempts);
        });

        //,err
        socket.on('reconnect_error', () => {
            console.error("socket io", 'reconnect error');
            this.connected = false;
        });

        //
        socket.on('reconnect_failed', () => {
            console.log("socket io", 'reconnect failed');
            this.connected = false;
            this.emit('close');
        });

        //error,err
        socket.on('error', (err) => {
            console.error("socket io", 'error');
            this.connected = false;
            this.emit('error', err);
        });

    }

    close() {
        if (this.socket) {
            this.socket.close();
        }
    }
}

export {
    Socket
}