import {
    Communicate,
    IconFont,
    Api,
    Socket
} from './index'
import {
    VideoStream,
    VideoClient
} from '@/plugins/WebVideoClient'
import BaseButton from './../components/BaseButton.vue'

const Plugin = {
    install(Vue) {
        Vue.component('BaseButton', BaseButton);
        Vue.component(IconFont.name, IconFont);

        Vue.prototype.$communicate = new Communicate();
        Vue.prototype.$api = new Api();
        Vue.prototype.$socket = new Socket();
        Vue.prototype.videoClient = new VideoClient();
        Vue.prototype.videoStream = new VideoStream();
    }
}

export default Plugin