<template>
  <div class="report-container">
    <div class="result">
      <p class="header">
        {{$t('report_completed')}}
      </p>
      <ul>
        <li
          v-for="(value,key) in list"
          :key="key"
        >
          <span>{{$t('report_'+key)}}</span>
          <span
            v-if="value.code==0"
            class="faild"
          >
            {{$t('report_undetected')}}
          </span>
          <span
            v-else-if="value.code == 1"
            class="success"
          >
            {{$t('report_passed')}}
            <IconFont
              class="icon-font"
              icon="iconzhengchangicon"
            />
          </span>
          <span
            v-else-if="value.code == 2"
            class="faild"
          >
            {{$t('report_failed')}}
            <IconFont
              class="icon-font"
              icon="iconyichangicon"
            />
          </span>
        </li>
      </ul>
      <p
        class="tips"
        :class="isSuccess?'success':'faild'"
        v-html="config.tips"
      ></p>
    </div>
    <div class="button-box">
      <base-button
        v-if="config.goBackText"
        class="ko"
        @click.native="goBack"
      >{{config.goBackText}}</base-button>
      <base-button
        v-if="config.goAheadText"
        class="ok"
        @click.native="goAhead"
      >{{config.goAheadText}}</base-button>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    config: {
      type: Object,
      required: true
    },
    data: {
      type: Object,
      required: true
    }
  },
  data() {
    return {

    }
  },
  computed: {
    list() {
      let network = {};
      if (this.data.course.code == this.data.socket.code == this.data.video.code == this.data.download.code == 1) {
        network.code = 1;
      } else {
        network.code = 2;
      }
      return {
        network,
        videoinput: this.data.videoinput,
        audioinput: this.data.audioinput,
        audiooutput: this.data.audiooutput,
        browser: this.data.browser
      }
    },
    isSuccess() {
      let isSuccess = false;
      if (this.list.network.code == 1 && this.list.videoinput.code == 1 && this.list.audioinput.code == 1 && this.list.audiooutput.code == 1 && this.list.browser.code == 1) {
        isSuccess = true;
      }
      return isSuccess;
    }
  },
  methods: {
    resetTest() {
      this.$emit('reset');
    },
    goBack() {
      this.$emit('goBack');
    },
    goAhead() {
      this.$emit('goAhead');
    }
  }
}
</script>

