<template>
  <div class="fill">
    <template v-if="cameras.length">
      <div class="list">
        {{ $t("cam_list_label") }}
        <DropDownList
          class="dropdown-list"
          @change="listChange"
          :list="cameras"
          :value="cameraItem"
        ></DropDownList>
        <base-button
          class="ko"
          @click="reloadDevice"
        >{{ $t("device_refresh") }}</base-button>
      </div>

      <p class="error">{{error_tip}}</p>
      <div v-if="!deviceAllow">
        <span>{{ $t("device_title_a") }}</span>
        <span v-html="$t('device_title_b')"></span>
        <img :src="src" />

        <base-button
          class="ko"
          @click="resultClick(false)"
        >{{ $t("button_next") }}</base-button>
      </div>

      <video
        id="videoview"
        class="view"
        v-show="deviceAllow"
        autoplay
      ></video>
      <span
        class="status"
        v-if="deviceAllow"
      >{{ $t("cam_allow_tip") }}</span>

      <div
        class="btn-groups"
        v-if="deviceAllow"
      >
        <base-button
          class="ko"
          @click="resultClick(false)"
        >{{ $t("button_ko") }}</base-button>
        <base-button
          class="ko"
          @click="resultClick(true)"
        >{{ $t("button_ok") }}</base-button>
      </div>
    </template>

    <template v-if="!cameras.length">
      <h4>{{ $t("cam_notfound_title") }}</h4>
      <span>{{ $t("cam_notfound_step1") }}</span>
      <span>{{ $t("cam_notfound_step2") }}</span>
      <span>{{ $t("cam_notfound_step3") }}</span>
      <div class="btn-groups next">
        <base-button
          class="ok"
          @click="reloadDevice"
        >{{ $t("device_refresh") }}</base-button>

        <base-button
          class="ko"
          @click="resultClick(false)"
        >{{ $t("button_next") }}</base-button>
      </div>
    </template>
  </div>
</template>

<script>
import DropDownList from '@/components/DropDownList.vue';
import browser_setting_ask_cn from './../assets/images/browser_setting_ask_cn.gif';
import browser_setting_ask_en from './../assets/images/browser_setting_ask_en.gif';

export default {
  name: "Camera",
  components: {
    DropDownList
  },
  data() {
    return {
      srclist: {
        zh_CN: browser_setting_ask_cn,
        en_US: browser_setting_ask_en
      },
      error: null,
      error_tip: null,
      deviceAllow: true,
      cameraItem: {},
      microphoneItem: {},
      cameras: [],
      microphones: []
    }
  },
  computed: {
    src() {
      return this.srclist[this.$i18n.locale];
    }
  },
  methods: {
    init() {
      if (this.videoStream) {
        this.deviceAllow = this.videoStream.access;
        this.videoStream.close();
        this.cameras = this.videoStream.cameras;
        this.microphones = this.videoStream.microphones;
        if (this.cameras.length) {
          this.changeDevice(this.cameras[0]);
        }
      }
    },
    reloadDevice() {
      const that = this;
      this.videoStream.init().then(this.init).catch(err => {
        that.error = err;
        console.log(err);
      });
    },
    changeDevice(item) {
      this.cameraItem = item;
      var that = this;
      if (this.videoStream) {
        this.videoStream.close();
        this.videoStream.cameraId = item.deviceId;
        this.videoStream.microphoneId = null;
        this.videoStream.videoProfile = "360P_11";
        this.videoStream.streamID = Math.floor(Math.random() * 100000);
        this.videoStream.createStream().then(function (stream) {
          item.code = 1;
          that.error = null;
          that.deviceAllow = that.videoStream.access;
          that.$nextTick().then(() => {
            that.videoStream.display(document.querySelector('#videoview'));
          });
          that.error_tip = null;
        }).catch(function (err) {
          that.deviceAllow = that.videoStream.access;
          that.error = err;
          item.code = 2;

          switch (err) {
            case 'NotAllowedError':
              that.error_tip = that.$t("cam_faild_tip");
              break;
            case 'NotReadableError':
              that.error_tip = that.$t("cam_faild_use_tip");
              break;
          }
        });
      }
    },
    listChange(item) {
      this.changeDevice(item);
    },
    resultClick(state) {
      this.videoStream.close();
      this.$emit('complete', {
        code: state ? 1 : 2,
        device: Object.assign({}, this.cameraItem),
        devices: this.cameras.filter(item => delete item.deviceId),
        info: this.error
      });
    }
  },
  mounted() {
    this.init();
  }
}
</script>

