<template>
  <button
    type="button"
    name="button"
    @click="clickHandler"
  >
    <slot></slot>
  </button>
</template>
<script>
export default {
  name:'BaseButton',
  methods:{
    clickHandler(){
      this.$emit('click', this);
    }
  }
}
</script>

