<template>
  <div class="network-container">
    <ul>
      <li v-for="(item,index) in list" :key="index">
        <IconFont class="icon-font" :icon="item.icon"/>
        {{item.text}}
        <IconFont
          v-if="item.status"
          class="icon-font-status"
          :icon="item.status==1?'iconzhengchangicon':'iconyichangicon'"
        />
      </li>
    </ul>
    <p v-if="browser.code==2">
      <span v-html="$t('browser_is_imcompatible')"></span>
      <a :href="$t('browser_download_href')">{{$t('browser_download')}}</a>
    </p>
    <base-button
      v-if="hasReadyToSkip==15||isTimeout"
      class="ko"
      @click.native="complete"
    >{{$t('button_next')}}</base-button>
  </div>
</template>

<script>
import { setTimeout } from 'timers';

const SKIPTIMEOUT = 5000;
const DOWNLOADTIMEOUT = 12000;

export default {
  props: {
    config: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      list: [
        {
          icon: 'iconjiaocai',
          text: this.$t('network_loading_the_courseware'),
          status: 0,//0 for detecting 1 for success 2 for failed
          info: ''
        },
        {
          icon: 'iconxiaoxi',
          text: this.$t('network_sending_the_messages'),
          status: 0,
          info: ''
        },
        {
          icon: 'iconshipin',
          text: this.$t('network_video_connecting'),
          status: 0,
          info: ''
        },
        {
          icon: 'iconyinpin',
          text: this.$t('network_audio_connecting'),
          status: 0,
          info: ''
        },
      ],
      course: { code: 0, url: this.config.courseware_url, time: "" }, //
      download: { code: 0, url: this.config.audio_url, time: "" }, //
      socket: { code: 0, url: this.config.socket.url, path: this.config.socket.path, time: "" }, //socket
      video: { code: 0, appId: this.config.agora_key, time: "", info: "" },  //
      browser: { code: 0, label: "", version: "", ua: "" },
      startTime: "",
      hasReadyToSkip: 0,
      isTimeout: false
    }
  },
  methods: {
    getBrowserInfo() {
      var Sys = {};
      var ua = navigator.userAgent.toLowerCase();
      var re = /(msie|firefox|chrome|opera|version).*?([\d.]+)/;
      var m = ua.match(re);
      Sys.label = m[1].replace(/version/, "safari");
      Sys.version = m[2];
      Sys.ua = ua;
      return Sys;
    },
    updateStatus(index, status, text) {
      this.list[index].status = status;
      this.list[index].text = text;
      switch (index) {
        case 0:
          this.hasReadyToSkip += 1;
          break;
        case 1:
          this.hasReadyToSkip += 2;
          break;
        case 2:
          this.hasReadyToSkip += 4;
          break;
        case 3:
          this.hasReadyToSkip += 8;
          break;
      }
    },
    async fetchCourse() {
      const { status } = await this.$api.fetchSource(this.config.courseware_url + "?v=" + this.startTime)
        .catch(() => {
          this.course.code = 2;
          this.updateStatus(0, 2, this.$t('network_failure_in_loading_the_courseware'));
        });
      if (status == 200) {
        this.course.code = 1;
        this.course.time = Date.now() - this.startTime;
        this.updateStatus(0, 1, this.$t('network_success_in_loading_the_courseware'));
      }
    },
    initSocket() {
      this.$socket.init({ ...this.config.socket });
      this.$socket.on('connected', () => {
        this.socket.code = 1;
        this.socket.time = Date.now() - this.startTime;
        this.updateStatus(1, 1, this.$t('network_success_in_loading_the_messages'));
        this.$socket.close();
      })

      this.$socket.on('reconnect', count => {
        if (count >= 2) {
          this.socket.code = 2;
          this.updateStatus(1, 2, this.$t('network_failure_in_loading_the_messages'));
          this.$socket.close();
        }
      });

      this.$socket.on('error', err => {
        this.socket.code = 2;
        this.updateStatus(1, 2, this.$t('network_failure_in_loading_the_messages'));
      });
    },
    initVideo() {
      if (!this.config) {
        console.error('');
        return;
      }
      const appId = this.config.agora_key;
      const channel = this.config.channelName;
      const appToken = this.config.token;
      this.videoClient.init(appId).then(() => {
        this.videoClient.connect(channel, appToken).then(() => {
          this.video.code = 1;
          this.video.time = Date.now() - this.startTime;
          this.updateStatus(2, 1, this.$t('network_success_in_video_connection'));
          this.videoClient.exit();
          this.videoClient = null;
        }).catch((err, info) => {
          this.video.code = 2;
          this.video.info = info;
          this.updateStatus(2, 2, this.$t('network_failure_in_video_connection'));
        });
      }).catch((err, info) => {
        this.video.code = 2;
        this.video.info = info;
        this.updateStatus(2, 2, this.$t('network_failure_in_video_connection'));
      });
    },
    async fetchAudio() {
      const { status } = await this.$api.fetchSource(this.config.audio_url + "?v=" + Date.now(), { timeout: DOWNLOADTIMEOUT })
        .catch(err => {
          this.download.time = '-1';
          this.download.code = 2;
          this.updateStatus(3, 2, this.$t('network_failure_in_audio_connection'));
        });
      if (status == 200) {
        this.download.code = 1;
        this.download.time = Date.now() - this.startTime;
        this.updateStatus(3, 1, this.$t('network_success_in_audio_connection'));
      }
    },
    checkBrowser() {
      let [code, info] = [1, ""];
      const Browser = this.getBrowserInfo();
      this.browser = { code: 0, ...Browser };
      if (Browser.label != 'chrome' || (Browser.label == 'chrome') && (Number(Browser.version.slice(0, 2)) < 59)) {
        this.browser.code = 2;
      } else {
        this.browser.code = 1;
      }
    },
    countdown() {
      setTimeout(() => {
        this.isTimeout = true;
      }, SKIPTIMEOUT)
    },
    complete() {
      this.$socket.close();
      this.$emit('complete', this.course, this.download, this.socket, this.video, this.browser);
    }

  },
  mounted() {
    this.startTime = Date.now();
    this.countdown();
    this.fetchCourse();
    this.initSocket();
    this.initVideo();
    this.fetchAudio();
    this.checkBrowser();
  },
  destroyed() {
    this.$socket.removeAllListeners();
  }
}
</script>

<style lang="less" scoped>
.network-container {
  width: 600px;
  height: 380px;
  position: relative;
  ul {
    margin-top: 78px;
    margin-left: 166px;
    li {
      line-height: 50px;
      display: flex;
      align-items: center;
      font-size: 18px;
      .icon-font {
        width: 35px;
        height: 35px;
        margin-right: 20px;
      }
      .icon-font-status {
        margin-left: 15px;
        width: 28px;
        height: 28px;
      }
    }
  }
  p {
    margin-top: 30px;
    text-align: center;
    font-size: 14px;
    color: #d73730;
  }
  .ko {
    margin-left: 208px;
    position: absolute;
    bottom: 36px;
  }
}
</style>


