<template>
  <div class="headphone-container">
    <p v-html="$t('headphone_listen_tips')"></p>
    <img
      v-if="loading"
      width="120"
      :src="loadingSrc"
      alt="loading"
    >
    <img
      v-else
      width="120"
      :src="listening"
      alt="listening"
    >
    <audio
      ref="audio"
      :src="music"
      autoplay
      loop
    ></audio>
    <p class="strong"><strong>{{$t('headphone_can_u_hear_the_audio')}}</strong></p>
    <template v-if="!loading">
      <base-button
        class="ko"
        @click.native="$emit('complete',false)"
      >{{$t('button_ko')}}</base-button>
      <base-button
        class="ko"
        @click.native="$emit('complete',true)"
      >{{$t('button_ok')}}</base-button>
    </template>
  </div>
</template>

<script>
import listening from '../assets/images/rap.gif'
import loadingSrc from '../assets/images/loading.gif'
export default {
  props: {
    music: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      loading: true,
      loadingSrc,
      listening
    }
  },
  mounted() {
    this.$refs.audio.addEventListener('canplay', () => {
      this.loading = false;
    })
  }
}
</script>

<style lang="less" scoped>
.headphone-container {
  text-align: center;
  width: 400px;
  margin-left: 80px;
  margin-top: 60px;

  p {
    font-size: 14px;
    color: rgba(121, 121, 121, 1);
    line-height: 20px;
    &.strong {
      font-size: 16px;
      font-weight: 500;
      color: rgba(33, 33, 33, 1);
    }
  }
}
</style>


