import * as Fingerprint from 'fingerprintjs'
import BaseApiService from '@/service/BaseApiService'

class Api extends BaseApiService {
    constructor() {
        super();
    }

    getNetworkConfig = () => this.request('/classroom/network/setting');

    /**
     * @param {String} url 
     * @param {Object} config 
     */
    fetchSource = (url, config = {}) => this.axios.get(url, config);

     /**
     * 
     * @param {*} camera        
     * @param {*} microphone    
     */
    saveDevice(camera, microphone) {
        let options = {
            hash: new Fingerprint({ canvas: true, screen_resolution: true }).get(),
            camera: camera && camera.deviceId,
            microphone: microphone && microphone.deviceId
        }
        return this.request('/classroom/network/room_device', options);
    }
}

export {
    Api
}