<template>
  <div class="wrapper">
    <ProgressBar class="progress-bar" :activeStep="activeStep" />
    <transition
      mode="out-in"
      :duration="400"
      enter-active-class="animated bounceInRight"
      leave-active-class="animated bounceOutLeft"
    >
      <Network
        v-if="activeStep == 0 && networkConfig"
        :config="networkConfig"
        @complete="networkComplete"
      />

      <device v-if="activeStep == 1 && !deviceEnabled" @complete="deviceComplete"></device>

      <camera ref="camera" v-if="activeStep == 1 && deviceEnabled" @complete="cameraComplete"></camera>

      <microphone ref="microphone" v-if="activeStep == 2" @complete="micComplete"></microphone>

      <Headphone
        v-if="activeStep == 3 && networkConfig"
        :music="networkConfig.sound_url"
        @complete="audioComplete"
      />
      <Report
        v-if="activeStep == 4"
        :config="reportConfig"
        :data="data"
        @reset="resetTestHandler"
        @goBack="goBack"
        @goAhead="goAhead"
      />
    </transition>
    <div v-if="getNetworkConfigError" class="error-contianer">
      <h4>{{$t('network_error')}}</h4>
      <base-button class="ko" @click.native="resetTestHandler">{{$t('button_test_again')}}</base-button>
    </div>
  </div>
</template>

<script>
import ProgressBar from './ProgressBar.vue';
import Network from './Network.vue';
import Device from './Device.vue';
import Camera from './Camera.vue';
import Microphone from './Microphone.vue';
import Headphone from './Headphone.vue';
import Report from './Report.vue';

export default {
  name: 'Home',
  components: {
    ProgressBar,
    Network,
    Device,
    Camera,
    Microphone,
    Headphone,
    Report
  },
  data() {
    return {
      activeStep: 0,
      networkConfig: null,
      deviceEnabled: false,
      reportConfig: {},
      getNetworkConfigError: false,
      data: {
        // code int, 0: , 1:,2:; time:-1
        "course": { code: 0, url: "", time: "" }, //
        "download": { code: 0, url: "", time: "" }, //
        "socket": { code: 0, url: "", path: "", time: "" }, //socket
        "video": { code: 0, appId: "", time: "", info: "" },  //
        "browser": { code: 0, label: "", version: "", ua: "" }, //
        "videoinput": { code: 0, devices: [{ label: '', code: "0|1|2" }], device: { label: "", code: "0|1|2" }, info: "" },//0,1,2
        "audioinput": { code: 0, devices: [{ label: '', code: "0|1|2" }], device: { label: "", code: "0|1|2" }, info: "" },//0,1,2
        "audiooutput": { code: 0, url: "", time: "", info: "" },
      }
    }
  },
  methods: {
    networkComplete(course, download, socket, video, browser) {
      this.data = { ...this.data, course, download, socket, video, browser }
      this.activeStep = 1;
    },
    deviceComplete(enabled) {
      this.activeStep = 1;
      this.deviceEnabled = true;
    },
    cameraComplete(data) {
      this.activeStep = 2;
      this.data['videoinput'] = data;
    },
    micComplete(data) {
      this.data['audioinput'] = data;
      this.activeStep = 3;
    },
    audioComplete(status) {
      if(Number(this.data['videoinput'].code) === 1 && Number(this.data['audioinput'].code) === 1){
        this.$api.saveDevice(this.data['videoinput'].device, this.data['audioinput'].device);
      }
      this.data.audiooutput.code = status ? 1 : 2;
      this.$emit('ready');
      this.$communicate.report(this.data);
      this.activeStep = 4;
    },
    resetTestHandler() {
      this.$emit('reset');
    },
    async getNetworkConfig() {
      const { isSuccess, result } = await this.$api.getNetworkConfig().catch(() => {
        this.getNetworkConfigError = true;
      });
      if (isSuccess) {
        this.networkConfig = result;
      } else {
        this.getNetworkConfigError = true;
      }
    },
    goBack() {
      this.$communicate.goBack();
    },
    goAhead() {
      this.$communicate.goAhead();
    },
    listen() {
      this.$communicate.once('config', (config = {}) => {
        this.reportConfig = config;
      });
    }
  },
  created() {
    this.getNetworkConfig();
    this.listen();
  }
}
</script>

