  width: 100%;
  height: 100%;
  position: inherit;
  overflow: hidden;

  .progress-bar {
    margin-left: 63px;
    margin-top: 41px;
  }
  .hide {
    display: none;
  }

  .show {
    display: block;
  }

  .error-contianer {
    width: 450px;
    text-align: center;
    margin-top: 100px;
    margin-left: 100px;
    h4 {
      color: @color-assist;
    }
    button {
      margin-top: 200px;
    }
  }
}
</style>

