<template>
  <ul
    class="progress-bar"
    :style="{
      'border-right':
        '15px solid ' +
        (activeStep == list.length - 1 ? '#D73730' : 'transparent')
    }"
  >
    <li
      v-for="(item, index) in list"
      :key="index"
      :class="{
        past: index < activeStep || activeStep == list.length - 1,
        active: index == activeStep && activeStep != list.length - 1
      }"
    >
      {{ item.name }}
    </li>
  </ul>
</template>

<script>
export default {
  name: 'ProgressBar',
  props: {
    activeStep: {
      default: 0
    }
  },
  data() {
    return {
      list: [
        { name: this.$t('progress_network_detect') },
        { name: this.$t('progress_camera_detect') },
        { name: this.$t('progress_microphone_detect') },
        { name: this.$t('progress_headphone_detect') },
        { name: this.$t('progress_report') },
      ]
    }
  },
}
</script>

<style lang="less" scoped>
@import url("../assets/style/variables.less");
.progress-bar {
  border-radius: 15px;
  background: rgba(227, 244, 255, 1);
  display: inline-flex;
  border-left: 15px solid @color-assist;
  font-size: 14px;
  li {
    width: 101px;
    line-height: 30px;
    position: relative;
    text-align: center;
    color: #797979;
    &.past,
    &.active {
      color: white;
      background: @color-assist;
    }
    &.active {
      width: 86px;
      margin-right: 15px;
      &:after {
        content: "";
        position: absolute;
        right: -15px;
        border-left: 15px solid @color-assist;
        border-top: 15px solid transparent;
        border-bottom: 15px solid transparent;
      }
    }
  }
}
</style>


