<template>
  <div class="fill">
    <template v-if="microphones.length">
      <div class="list">
        {{ $t("mic_list_label") }}
        <DropDownList
          class="dropdown-list"
          @change="listChange"
          :list="microphones"
          :value="microphoneItem"
        ></DropDownList>
        <base-button
          class="ko"
          @click="reloadDevice"
        >{{ $t("device_refresh") }}</base-button>
      </div>

      <p
        class="error"
        v-if="error_tip"
      >{{error_tip}}</p>

      <template v-if="!deviceAllow">
        <img :src="src" />
        <base-button
          class="ko"
          @click="resultClick(false)"
        >{{ $t("button_next") }}</base-button>
      </template>

      <template v-if="deviceAllow">
        <span>{{ $t("mic_title_a") }}</span>
        <span>{{ $t("mic_title_b") }}</span>
        <div class="view">
          <svg
            xmlns="http://www.w3.org/2000/svg"
            version="1.1"
            width="100%"
            height="100%"
          >
            <line
              x1="0"
              y1="44"
              x2="100%"
              y2="44"
            ></line>
            <line
              x1="0"
              y1="88"
              x2="100%"
              y2="88"
            ></line>
            <polyline
              class="level"
              :points="points"
              width="240"
              height="135"
            ></polyline>
          </svg>
        </div>
        <span class="status">{{ $t("mic_allow_tip") }}</span>

        <div class="btn-groups">
          <base-button
            class="ko"
            @click="resultClick(false)"
          >{{ $t("button_ko") }}</base-button>
          <base-button
            class="ko"
            @click="resultClick(true)"
          >{{ $t("button_ok") }}</base-button>
        </div>
      </template>
    </template>

    <template v-if="!microphones.length">
      <h4>{{ $t("mic_notfound_title") }}</h4>
      <span>{{ $t("mic_notfound_step1") }}</span>
      <span>{{ $t("mic_notfound_step2") }}</span>
      <span>{{ $t("mic_notfound_step3") }}</span>
      <div class="btn-groups next">
        <base-button
          class="ok"
          @click="reloadDevice"
        >{{ $t("device_refresh") }}</base-button>

        <base-button
          class="ko"
          @click="resultClick(false)"
        >{{ $t("button_next") }}</base-button>
      </div>
    </template>
  </div>
</template>

<script>
import DropDownList from '@/components/DropDownList.vue';
import browser_setting_ask_cn from './../assets/images/browser_setting_ask_cn.gif';
import browser_setting_ask_en from './../assets/images/browser_setting_ask_en.gif';

export default {
  name: "Microphone",
  components: {
    DropDownList
  },
  data() {
    return {
      points: [],
      intervalID: 0,
      srclist: {
        zh_CN: browser_setting_ask_cn,
        en_US: browser_setting_ask_en
      },
      error: null,
      error_tip: null,
      deviceAllow: true,
      cameraItem: {},
      microphoneItem: {},
      cameras: [],
      microphones: []
    }
  },
  computed: {
    src() {
      return this.srclist[this.$i18n.locale];
    }
  },
  methods: {
    init() {
      if (this.videoStream) {
        this.deviceAllow = this.videoStream.access;
        this.videoStream.close();
        this.cameras = this.videoStream.cameras;
        this.microphones = this.videoStream.microphones;
        if (this.microphones.length) {
          this.changeDevice(this.microphones[0]);
        }
      }
    },
    reloadDevice() {
      const that = this;
      this.videoStream.init().then(this.init).catch(err => {
        that.error = err;
        console.log(err);
      });
    },
    changeDevice(item) {
      this.microphoneItem = item;
      const that = this;
      if (this.videoStream) {
        this.videoStream.close();
        this.videoStream.cameraId = null;
        this.videoStream.microphoneId = item.deviceId;
        this.videoStream.videoProfile = "360P_11";
        this.videoStream.streamID = Math.floor(Math.random() * 100000);

        this.videoStream.createStream().then(function (stream) {
          that.deviceAllow = that.videoStream.access;
          that.error = null;
          item.code = 1;
          that.activeLevel(stream);
          that.error_tip = null;
        }).catch(function (err) {
          that.deviceAllow = that.videoStream.access;
          that.error = err;
          item.code = 2;

          switch (err) {
            case 'NotAllowedError':
              that.error_tip = that.$t("mic_faild_tip");
              break;
            case 'NotReadableError':
              that.error_tip = that.$t("mic_faild_use_tip");
              break;
          }
        });
      }
    },
    activeLevel(stream) {
      const that = this;
      var polylines = [];
      if (this.intervalID) {
        clearInterval(this.intervalID);
      }

      this.intervalID = setInterval(function (e) {
        const level = stream.getAudioLevel();
        var maxCount = 50;
        const width = 240, height = 135;
        var result = [];
        var num = level * height;
        polylines.unshift(Math.round(height - num));
        polylines.forEach(function (val, index) {
          result.push((maxCount - index) * width / maxCount + " " + val);
        });

        if (polylines.length >= maxCount) {
          result.pop();
          polylines.pop();
        }
        that.points = result.join(',');
      }, 100);
    },
    listChange(item) {
      this.changeDevice(item);
    },
    resultClick(state) {
      this.videoStream.close();
      if (this.intervalID) {
        clearInterval(this.intervalID);
      }
      this.$emit('complete', {
        code: state ? 1 : 2,
        device: Object.assign({}, this.microphoneItem),
        devices: this.microphones.filter(item => delete item.deviceId),
        info: this.error
      });
    }
  },
  mounted() {
    this.init();
  }
}
</script>

